import "../styles/globals.css";
import { Toaster } from "react-hot-toast";

export default function MyApp({ Component, pageProps }) {
  return (
    <>
      <Toaster
        position="top-right"
        toastOptions={{
          style: {
            background: "#FF6B00",
            color: "#fff",
            borderRadius: "8px",
          },
        }}
      />
      <Component {...pageProps} />
    </>
  );
}
